.diet-info
    display: flex
    width: 258px
    height: 125px
    background-color: #FBFBFB
    align-items: center
    gap: 24px
    border-radius: 5px
    padding: 32px
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.02)

.diet-info__icon
    width: 60px
    height: 60px

.diet-info__details
    display: flex
    flex-direction: column

.diet-info__value
    color: #282D30
    font-size: clamp(0.875rem, 0.4318rem + 0.9091vw, 1.25rem)
    font-weight: 700
    margin: 0

.diet-info__category
    color: #74798C
    font-size: clamp(0.6875rem, 0.4659rem + 0.4545vw, 0.875rem)
    font-weight: 500
    margin: 0

@media (max-width: 768px)
    .diet-info
        width: calc(50% - 8px) // Two items per row if space allows in a flex-wrap container
        height: auto // Adjust height automatically
        padding: 20px
        gap: 16px
        &__icon
            width: 48px
            height: 48px

@media (max-width: 600px)
    .diet-info
        width: 100% // Full width to stack vertically
        max-width: 300px // Optional: prevent excessive width on wide mobile if centered
        padding: 15px
        gap: 10px
        &__icon
            width: 40px
            height: 40px
